.card {
  display: flex;
  flex-direction: column;
  width: $card-width;
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;

  &__image {
    display: block;
    height: 134px;
    width: 160px;
    margin: 0 auto 40px;
  }

  &__title {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: $color-main-accent;
    margin-bottom: 4px;
  }

  &__product-code {
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: $color-secondary;
    margin-bottom: 16px;
  }

  &__reviews {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
  }

  &__review-count {
    font-weight: 400;
    font-size: 10px;
    line-height: 15px;
    color: $color-main-accent;
  }

  &__price {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 16px;
  }

  &__price-text {
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: $color-secondary;
  }

  &__price-amount {
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    color: $color-main-accent;
  }

  &__button {
    text-align: center;
    padding: 11px 0;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    color: #fff;
    background: $color-accent-blue;
    border-radius: 5px;
    text-decoration: none;
    border: 1px solid $color-accent-blue;
    transition: color 0.3s ease,
      background-color 0.3s ease;

    &:hover {
      color: $color-accent-blue;
      background-color: #fff;
    }
  }
}
